body {
  width: 100%;
  overflow-x: hidden;
}

.nav {
  position: absolute;
  height: 400px;
  left: 0px;
  top: 0px;
  
  background: #21222A;
  color: #61DAFB;
  width: 100%;
  height: 91px;
  display: flex;
  flex-direction: row;;
  align-items: center;
}

.logoDiv {
  margin-left: 5%;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 60px;
  margin-right: auto;
}

.logo {
  height: 60px;
  width: 60px;
}

h4 {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  font-size: 1.5em;
  line-height: 27px;
  letter-spacing: -0.05em;

}

.titleDiv {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  font-size: 1.2em;
  line-height: 19px;
  text-align: right;
  margin-right: 5%;
}


.main {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0px;
  top: 91px;

  background: #282D35;
  color: #FFFFFF;
}



.mainH1 {
    margin-left: 10%;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 5em;
    line-height: 47px;
    letter-spacing: -0.05em;
}

.mainUl {
  margin-left: 10%;
  list-style: none;
}

li {
  margin-bottom: 3%;
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 2em;
  line-height: 19px;
}


ul li::before {
  content: "\2022";  /* Add content: \2022 is the CSS Code/unicode for a bullet */
  color: #61DAFB;
  ; /* Change the color */
  font-weight: bolder; /* If you want it to be bold */
  font-size: 2em;
  transform:  translateY(0.15em);
  display: inline-block; /* Needed to add space between the bullet and the text */
  width: 1em; /* Also needed for space (tweak if needed) */
  margin-left: -1em; /* Also needed for space (tweak if needed) */
}

.backgroundLogo {
  position: absolute;
  height: 90%;
  top: 10%;
  transform: translateX(120%);
  filter: brightness(0) saturate(100%);
  filter: brightness(25%);
  
}